.saline-room {
  &__content {
    padding: 70px 0;
    padding-bottom: 100px;
    background: url('../img/shutterstock-657941563.png') no-repeat;
    background-size: cover;
    background-attachment: fixed;
    @include r(767) {
      padding-bottom: 30px;
      padding-top: 0; } }
  &__gallery {
    background: repeating-linear-gradient(to right,rgba(255, 255, 255, 0.02) 0%,rgba(255, 255, 255, 0.1) 50%,rgba(255, 255, 255, 0.02) 100%); }
  .owl-carousel .owl-nav button.owl-next, .owl-carousel .owl-nav button.owl-prev, .owl-carousel button.owl-dot {
    font: status-bar; }
  .owl-gallery {
    height: 105px;
    .owl-stage-outer, .owl-stage, .owl-item {
      height: 100%; }
    .owl-prev span, .owl-next span {
      position: relative;
      @include center;
      color: transparent;
      &:before {
        content: $icon-left;
        position: absolute;
        font-family: 'iconfont';
        color: $apple;
        font-size: 40px; } }
    .owl-next {
      position: absolute;
      right: -40px;
      top: 50%;
      transform: translate(0, -50%);
      span:before {
        transform: rotate(180deg); } }
    .owl-prev {
      position: absolute;
      left: -40px;
      top: 50%;
      transform: translate(0, -50%); }
    .item {
      overflow: hidden;
      &:before {
        content: '';
        width: 100%;
        position: absolute;
        left: 0;
        bottom: 0;
        height: 10px;
        background: $apple; }
      img {
        height: 100%;
        width: auto; } } }
  &__title {
    @include r(767) {
      padding-top: 1px; }
    p {
      font-size: 30px;
      font-weight: normal;
      line-height: 1.33;
      letter-spacing: 1px;
      color: $white;
      margin-bottom: 80px;
      max-width: 735px;
      @include r(767) {
        font-size: 20px; } }
    h1 {
      margin-bottom: 60px; } }
  &__block {
    @include r(767) {
      @include center;
      flex-direction: column; }
    &__img {
      min-height: 150px;
      @include center;
      justify-content: flex-start; }
    p {
      font-size: 24px;
      font-weight: 600;
      line-height: 1.42;
      letter-spacing: 0.8px;
      color: $white;
      max-width: 215px;
      @include r(767) {
        text-align: center; } } } }
.feel {
  position: relative;
  padding-top: 100px;
  padding-bottom: 120px;
  @include r(767) {
    padding-bottom: 0px; }
  &:before {
    content: '';
    position: absolute;
    bottom: 0;
    width: 0px;
    height: 0px;
    background: transparent;
    border-left: 700px solid $white;
    border-top: 90px solid transparent;
    @include r(767) {
      display: none; } }
  p {
    font-family: 'SF Pro Display';
    font-size: 24px;
    font-weight: normal;
    font-style: normal;
    line-height: 1.42;
    letter-spacing: 1px;
    color: $pale-olive;
    max-width: 255px;
    @include r(767) {
      margin-bottom: 30px; } }
  &__video {
    border-radius: 7px;
    border-bottom-right-radius: 90px;
    border-top-left-radius: 90px;
    transform: skewX(-11deg);
    height: 335px;
    width: 93%;
    overflow: hidden;
    @include r(767) {
      position: relative;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%) skewX(-11deg);
      width: 88%; }
    a {
      transform: skewX(11deg);
      height: 100%;
      width: 100%;
      display: block;
      position: relative;
      img {
        width: 110%;
        position: relative;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%); }
      i {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        font-size: 100px;
        color: $apple;
        transition: all 0.3s; }
      &:hover {
        i {
          color: $white; } } } } }
.features {
  background: $white;
  position: relative;
  padding: 120px 0;
  padding-bottom: 10px;
  overflow: hidden;
  @include r(767) {
    padding-top: 50px;
    .wow {
      animation-name: q !important; } }
  &__block {
    margin-bottom: 140px;
    margin-top: 50px;
    min-height: 400px;
    posr {}
    @include r(767) {
      margin-top: 0; }
    h3 {
      font-size: 40px;
      font-weight: bold;
      letter-spacing: 0.5px;
      color: $charcoal;
      margin-bottom: 65px; }
    p {
      font-family: 'SF Pro Display';
      font-size: 24px;
      font-weight: normal;
      font-style: normal;
      line-height: 1.42;
      letter-spacing: 1px;
      color: #333333;
      max-width: 440px;
      padding-bottom: 50px;
      @include r(767) {
        font-size: 20px; } }
    &__text {
      max-width: 445px;
      padding-left: 10px;
      @include r(767) {
        max-width: 100%;
        margin-top: 35px; }
      &-secondary {
        position: relative;
        left: 100%;
        transform: translate(-100%); } }
    &__img {
      position: relative;
      overflow: hidden;
      height: 400px;
      border-radius: 5px;
      @include r(767) {
        position: relative;
        width: 100%;
        transform: none;
        left: 0;
        right: 0; }
      &-primary {
        left: -50px;
        border-bottom-right-radius: 90px;
        transform: skewX(-12deg);
        .features__block__img__content {
          @include center;
          width: 125%;
          height: 100%;
          transform: skewX(12deg);
          img {
            width: 120%; }
          @include r(767) {
            transform: none; } } }
      &-secondary {
        right: -50px;
        border-bottom-left-radius: 90px;
        transform: skewX(12deg);
        .features__block__img__content {
          @include center;
          width: 125%;
          height: 100%;
          transform: skewX(-12deg);
          margin-left: -50px;
          img {
            width: 120%; }
          @include r(767) {
            transform: none;
            margin: 0; } } } } } }
