.schedule {
  padding-bottom: 100px;
  h1 {
    padding: 60px 0;
    margin: 0;
    @include r(767) {
      font-size: 50px; } }
  &__block {
    display: flex;
    padding: 0 10px;
    @include r(900) {
      flex-wrap: wrap;
      justify-content: space-between; }
    @include r(767) {
      flex-direction: column; }
    &-select {
      height: 40px;
      max-width: 255px;
      width: 100%;
      margin-right: 30px;
      margin-bottom: 20px;
      @include r(900) {
        margin: 0 20px 20px; }
      @include r(767) {
        max-width: 100%;
        margin: 0 !important; }
      &:last-child {
        margin: 0;
        @include r(900) {
          margin: 0 20px 20px; } } } }
  &__slider {
    position: relative;
    margin-top: 50px;
    @include r(1100) {
      padding-left: 60px; }
    .schedule_day {
      height: 111px;
      width: 100%; }
    b {
      font-family: 'SF Pro Text';
      font-size: 18px;
      font-weight: normal;
      font-style: normal;
      line-height: 1.44;
      text-align: center;
      color: $white;
      position: absolute;
      left: 25px;
      width: 48px;
      height: 150px;
      display: block;
      @include r(1100) {
        left: 5px; }
      .schedule__slider__b {
        position: absolute;
        left: -20px;
        top: -9px;
        width: 1300px;
        height: 100%;
        background: $white-two;
        opacity: 0.1; } }
    &__icon {
      display: flex;
      justify-content: flex-end;
      i {
        font-size: 20px;
        color: $white;
        margin-left: 10px; } }
    &__block {
      padding: 10px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      min-height: 150px;
      box-sizing: border-box;
      margin-bottom: 1px;
      position: relative;
      &.deactivation {
        &:before {
          content: '';
          position: absolute;
          left: 0;
          top: 0;
          width: 100%;
          height: 100%;
          background: #000;
          opacity: 0.6;
          z-index: 9; } }
      &__day {
        padding: 35px 15px;
        display: flex;
        justify-content: center;
        border-bottom: solid 1px $pale-olive;
        &.active {
          background: rgba($apple, 0.1); }
        &.fixed {
          position: absolute;
          z-index: 99;
          background-image: url('../img/bg_all.png');
          background-position: 50% 0;
          width: calc(100% - 29px); }
        &.active.fixed {
          background: rgb(38, 46, 32); }
        p {
          font-family: 'SF Pro Text';
          font-size: 14px;
          font-weight: bold;
          font-style: normal;
          line-height: 1.43;
          text-align: center;
          width: 95px;
          color: $apple; } }
      &.none {
        background: rgba($white-two, 0.1); }
      &.pool {
        background: #36bb45; }
      &.pool-2 {
        background: #309dce; }
      &.cave {
        background: #de8c2c; }
      &.realryder {
        background: #365ddd; }
      &.pilates {
        background: #9b4ef0; }
      &.pilates-2 {
        background: #ded02c; }
      h3 {
        font-family: 'SF Pro Text';
        font-size: 18px;
        font-weight: bold;
        font-style: normal;
        line-height: 1.11;
        color: $white;
        min-height: 60px;
        padding-bottom: 5px; }
      span {
        font-family: 'SF Pro Text';
        font-size: 13px;
        font-weight: normal;
        font-style: normal;
        line-height: 1.11;
        color: $white;
        min-height: 40px;
        display: block; } } } }
.schedule__slider {
  .swiper-button-next {
    transform: rotate(-180deg);
    right: 40px; }
  .swiper-button-prev {
    left: 40px; }
  .swiper-button-prev, .swiper-button-next {
    top: 70px;
    height: 20px;
    i {
      font-size: 20px; } } }

.schedule.boxing {
  padding: 100px 0 200px;
  position: relative;
  h1 {
    font-size: 80px;
    @include r(767) {
      font-size: 30px; } }
  &:before {
    content: '';
    position: absolute;
    right: 0;
    top: 0;
    width: 700px;
    height: 90px;
    background: $white;
    clip-path: polygon(0 0%, 100% 0%, 100% 100%);
    @include r(767) {
      display: none; } }
  &:after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 700px;
    height: 90px;
    background: $white;
    clip-path: polygon(0 0%, 0% 100%, 100% 100%);
    @include r(767) {
      display: none; } } }
