.schedule.boxing {
  padding: 100px 0 200px;
  position: relative;
}

.schedule.boxing h1 {
  font-size: 80px;
}

@media only screen and (max-width: 767px) {
  .schedule.boxing h1 {
    font-size: 30px;
  }
}

.schedule.boxing:before {
  content: '';
  position: absolute;
  right: 0;
  top: 0;
  width: 700px;
  height: 90px;
  background: #ffffff;
  clip-path: polygon(0 0%, 100% 0%, 100% 100%);
}

@media only screen and (max-width: 767px) {
  .schedule.boxing:before {
    display: none;
  }
}

.schedule.boxing:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 700px;
  height: 90px;
  background: #ffffff;
  clip-path: polygon(0 0%, 0% 100%, 100% 100%);
}

@media only screen and (max-width: 767px) {
  .schedule.boxing:after {
    display: none;
  }
}
